<style>
  .container {
    padding:10px;
  }
  .building_list {
    margin-top: 30px;
  }

  Table{
    width:800px;
  }
</style>
<template>
  <!--建筑物管理->建筑物-->
  <!--右侧内容总div-->
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
        <span class="page-title">建筑物</span>
      </iCol>
    </Row>
    <div class="back-to-list" v-if="canReturn" @click="returnPage"></div>
    <!--右侧工具栏，包括检索、添加等按钮-->
    <div class="tool-panel">
      <Input v-model="searchParam" class="grey" placeholder="输入名称" @on-enter="getBuildList(1)" style="width: 300px"></Input>
      <Button type="primary" @click="getBuildList(1)" class="grey" style="margin-left: 5px; width: 80px;">搜索</Button>
      <Button v-if="editable==true" type="primary" @click="openNewDevice" class="grey" style="margin-left: 5px; width: 80px;float: right">添加</Button>
    </div>
    <!--右侧表格模块-->
    <div class="body-table">
      <!--表格位置-->
      <Table :columns="columns" border :data="buildings"
             class="auto-height-table grey" style="width:100%"></Table>
      <!--分页位置-->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page @on-change="getBuildList" class="default" :total="pageTotal" :current="pageNum" :pageSize="pageSize" :show-total="true"></Page>
        </div>
      </div>
    </div>
    <Modal v-model="addModal" title="创建新的建筑物" width="800" class="default" :maskClosable="false" @on-cancel="cancel">
      <Form ref="newBuildingForm" :model="newBuilding" label-position="right" :label-width="80" :rules="newBuildingValidate">
        <Form-item prop="buildingName" label="名称：">
          <Input v-model="newBuilding.buildingName" class="grey" style="width: 213px;" type="text" placeholder="输入建筑名称" />
        </Form-item>
        <Row style="margin-top: 0px;">
          <iCol span="23">
            <myarea ref="myarea" @select="myareaOut" :address="address" class="default"></myarea>
          </iCol>
          <iCol span="1">
          </iCol>
        </Row>
        <Row :gutter="7" style="margin-top: 0px;">
          <iCol span="23">
          <form-item prop="addrStreet" :label-width="120"  label="详细地址：" style="margin-left: -40px">
            <Input  v-model="newBuilding.addrStreet" class="grey" placeholder="输入街道及详细地址"/>
          </form-item>
          </iCol>
          <iCol span="1">
          <form-item :label-width="10">
            <a @click="relocate"><img src="../../../assets/building/building_location.png" style="padding:0px;"></a>
          </form-item>
          </iCol>
        </Row>
      </Form>
      <div slot="footer">
        <Button class="grey cancel" @click="cancel">取消</Button>
        <Button class="grey" @click="addBuilding('newBuildingForm')">保存</Button>
      </div>
    </Modal>
    <!-- 创建用户弹出层END -->
    <map-locator-modal :showModal="showMapLocator" :defaultCenter="mapLocatorDefaultCenter" :defaultLocation="buildingCoordination" @CloseMapLocatorModal="onCloseMapLocatorModal" @UpdateLocation="onLocationUpdated" ></map-locator-modal>
  </div>
</template>


<script>
  import myArea from '../../../components/vue-myarea/area.vue';
  import server from "../../../config/api";
  import MapLocatorModal from '../../../components/map-locator2.vue';
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';

  let isInit;

  export default {
    data() {
      return {
        canReturn: this.getYunSessionLength('route_list') > 1,
        baseInfo:{},
        pageSize:20,
        pageNum: undefined,
        organizationId:'',
        showMapLocator:false,
        addModal: false,
        self: this,
        searchParam: '',
        pageTotal: undefined,
        buildings: [],
        newBuilding: {},
        orgClass:'NORMAL_ENTITY',
        columns: [
          {
            title: '序号',
            type: 'index',
            align: 'center',
            width:50,
          },
          {
            title: '建筑物名称',
            key: 'buildingName',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('a', {
                  on: {
                    click: () => {
                      this.openBuilding(params.row.buildingId);
                    }
                  }
                }, params.row.buildingName)
              ]);
            }
          },
          {
            title: '地址',
            key: 'address',
            align: 'center'
          }
        ],
        address: {},
        newBuildingValidate: {
          buildingName: [
            {required: true, message: '建筑物名字不能为空', trigger: 'blur'}
          ],
          addrProv: [
            {required: true, message: '请选择省份', trigger: 'blur'}
          ],
          addrCity: [
            {required: true, message: '请选择城市', trigger: 'blur'}
          ],
          addrDistrict: [
            {required: true, message: '请选择县区', trigger: 'blur'}
          ],
          addrStreet: [
            {required: true, message: '详细地址不能为空', trigger: 'blur'}
          ],
        }
      }
    },

    components: {
      'myarea': myArea,
      'map-locator-modal': MapLocatorModal,
    },

    methods: {
      relocate: function(){
        this.showMapLocator = true;
      },
      onCloseMapLocatorModal: function(){
        this.showMapLocator = false;
      },
      onLocationUpdated: function(address) {
        let building = this.newBuilding;
        building.mapLng = address.location[0];
        building.mapLat = address.location[1];
        this.newBuilding = {};
        Vue.nextTick(() => {
          this.newBuilding = building;
        });
      },
//      returnUpperLevel: function () {
//        // 返回列表
//        this.$router.push({
//          path: '/archives',
//        });
//      },
      returnPage: function() {
        // 根据sessionStorage返回上一页
        this.popYunSession('route_list');
        let page = this.topYunSession('route_list');
        this.$router.push(page.router);
      },
      myareaOut: function(d) {
        this.address = d;
        console.log(this.address);
      },
      delBuilding:function (buildingId) {
        if(this.buildings.length==1 && this.pageNum !=1){
          this.pageNum=this.pageNum-1;
        }
        this.$http.delete(server.api.organization.building.delBuild.replace('{buildingId}',buildingId)).then((response)=>{
          this.getBuildList(this.pageNum);
          this.$Message.success("删除建筑物成功");
        },(error)=>{
          this.$Message.error("删除建筑物失败");
        })
      },
      openBuilding: function(buildingId) {
        if(this.$route.meta.force_readonly_mode === true){
          this.$router.push({path: '/detail/building_detail',query:{buildingId:buildingId, tableName:'baseInfo',organizationId:this.organizationId}});
        } else {
          this.$router.push({path: '/company/building_detail',query:{buildingId:buildingId, tableName:'baseInfo',organizationId:this.organizationId}});
        }
        },
      showSelectUserModal: function() {
        this.searchUserParam = undefined;
        this.users = [];
        this.selectedUserId = undefined;
        this.usersTotal = 0;
        this.selectUserModal = true;
      },
      addBuilding:function (name) {
        this.newBuilding.orgId=this.organizationId;
        this.newBuilding.addrProv=this.address.addrProv;
        this.newBuilding.addrCity=this.address.addrCity;
        this.newBuilding.addrDistrict=this.address.addrDistrict;
        this.$refs[name].validate((valid)=>{
          if(valid){
            this.$http.post(server.api.organization.building.addBuild,this.newBuilding).then((response)=>{
              this.addModal=false;
              this.getBuildList(this.pageNum);
              this.$Message.success("建筑物添加成功");
            },(error)=>{
              this.$Message.error("建筑物添加失败");
            });
          } else{
            this.$Message.error('表单验证失败!');
          }
        })
      },
      createBuildTable:function () {
        //单位地址取得
        this.getOrgAddress();
        this.getBuildList(1);
      },
      getOrgAddress:function () {
        this.$http.get(server.api.organization.baseInfo.get.replace('{orgId}', this.organizationId) + "?orgClass="+this.orgClass).then((response)=>{
          this.baseInfo = response.body.data;
        },(error)=>{
          this.$Message.error('单位管理基本信息显示失败!');
        });
      },
      getBuildList:function (pageNum) {
        this.$http.get(server.api.organization.buildingList.list.replace('{orgId}',this.organizationId) + '?buildingName='+this.searchParam +"&pageSize="+this.pageSize+"&pageNum="+pageNum).then((response)=>{
          this.buildings = response.body.data.rows;
          this.pageTotal = response.body.data.totalCount;
          this.pageNum = pageNum;
          if(isInit) {
            isInit = false;
            if(this.pageTotal === 1) {
              // @Lucas 初始化进入，如果只有一个建筑物信息直接进入建筑物详情页。
              this.openBuilding(response.body.data.rows[0].buildingId);
              return;
            }
          }
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          });
        },(error)=>{
          this.$Message.error('建筑物列表获取失败');
        })
      },
      cancel:function () {
        this.$refs['newBuildingForm'].resetFields();
        this.addModal=false;
      },
      openNewDevice:function () {
        setTimeout(() => {
          this.newBuilding={};
          this.newBuilding.addrStreet=this.baseInfo.addrStreet;
          this.address.addrProv=this.baseInfo.addrProv;
          this.address.addrCity=this.baseInfo.addrCity;
          this.address.addrDistrict=this.baseInfo.addrDistrict;
          this.$refs.myarea.initData();
          this.addModal=true;
        }, 300);
      }
    },
    computed:{
      editable: function() {
        return this.hasPermission(this.$YDAuthCode.NormalEntity.BuildingMgmt);
      },
      mapLocatorDefaultCenter : function(){
        if(this.newBuilding == null || this.newBuilding.mapLng == null || this.newBuilding.mapLat == null){
          return  [121.5300857178, 38.8626430801];
        }else{
          return [this.newBuilding.mapLng ,this.newBuilding.mapLat];
        }
      },
      buildingCoordination: function(){
        if(this.newBuilding == null || this.newBuilding.mapLng == null || this.newBuilding.mapLat == null){
          return [];
        }else{
          return [this.newBuilding.mapLng ,this.newBuilding.mapLat];
        }
      },
    },
    mounted(){
      if(this.editable){
        this.columns.push(
          {
            title: '操作',
            align: 'center',
            width:80,
            render: (h, params) => {
              return h('div', [
                h('i-button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {
                    color: '#f9caca',
                  },
                  on: {
                    click: () => {
                      this.delBuilding(params.row.buildingId)
                    }
                  }
                }, '移除')
              ]);
            }
          }
        )
      }
      let page = this.topYunSession('route_list');
      if(page === null || page.orgId === undefined) {
        // 根节点
        this.organizationId = localStorage.current_org_id;
      } else {
        this.organizationId = page.orgId;
      }
      this.createBuildTable();
      Vue.nextTick(() => {
        $(".auto-height-table .ivu-table-body").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
      });
    },
    beforeRouteEnter(to, from, next) {
      // 从建筑物详情返回，不会自动进入建筑物详情页
      isInit = from.name !== 'buildingDetail';
      next();
    },
  }
</script>



